import { Component, Prop, State, Event, EventEmitter, h } from '@stencil/core';

export type TagVariant = 'text-only' | 'avatar';
export type TagSize = 'sm' | 'md' | 'lg';

@Component({
  tag: 'bh-tag',
  styleUrl: 'bh-tag.css',
  shadow: true,
})
export class BhTag {
  /** Tag variant */
  @Prop() variant: TagVariant = 'text-only';

  /** Tag size */
  @Prop() size: TagSize = 'md';

  /** Tag label text */
  @Prop() label: string = 'Label';

  /** Whether the tag is dismissible */
  @Prop() dismissible: boolean = false;

  /** Whether the tag is disabled */
  @Prop() disabled: boolean = false;

  /** Maximum width for the label (in pixels) */
  @Prop() maxWidth: number = 120;

  /** Avatar image source (for avatar variant) */
  @Prop() avatarSrc: string = '';

  /** Internal dismissing state */
  @State() isDismissing: boolean = false;

  /** Emitted when dismiss button is clicked */
  @Event() bhDismiss!: EventEmitter<void>;

  private handleDismiss = (event: MouseEvent) => {
    event.stopPropagation();

    if (this.dismissible && !this.disabled && !this.isDismissing) {
      this.isDismissing = true;

      // Simulate animation duration
      setTimeout(() => {
        this.bhDismiss.emit();
        this.isDismissing = false;
      }, 200);
    }
  };

  render() {
    const classes = {
      'tag': true,
      [`tag-${this.size}`]: true,
      [`tag-${this.variant}`]: true,
      'tag-dismissible': this.dismissible,
      'tag-disabled': this.disabled,
      'animate-dismiss': this.isDismissing,
    };

    const labelStyle = {
      maxWidth: `${this.maxWidth}px`,
    };

    return (
      <span class={classes}>
        <div class="tag-content">
          {/* Avatar variant */}
          {this.variant === 'avatar' && (
            <bh-avatar size="xxs" type={this.avatarSrc ? 'image' : 'placeholder'} imageSrc={this.avatarSrc}></bh-avatar>
          )}

          {/* Label */}
          <span class="tag-label-wrapper">
            <span class="tag-label" style={labelStyle}>
              {this.label}
            </span>
          </span>
        </div>

        {/* Dismiss button (if dismissible) */}
        {this.dismissible && (
          <button
            class="tag-dismiss"
            onClick={this.handleDismiss}
            disabled={this.disabled}
            type="button"
            aria-label="Dismiss"
          >
            <span class="material-symbols-outlined tag-dismiss-icon">
              close
            </span>
          </button>
        )}
      </span>
    );
  }
}
